<template>
    <div class="hello">
        <el-form
                label-width="80px"
                label-position="left"
                ref="userInfo"
                :rules="rules"
                :model="userInfo"
                class="form-ceshi"
        >
            <el-form-item label="姓名" prop="username">
                <el-input v-model="userInfo.username"></el-input>
            </el-form-item>
            <el-form-item label="年龄" prop="age">
                <el-input v-model="userInfo.age"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit('userInfo')">查询</el-button>
            </el-form-item>
        </el-form>
        <div class="table-ceshi">
            <el-button type="primary" @click="getInfo">拉取</el-button>
            <br>
            <br>
            <br>
            <br>
            <el-table
                    :data="tableData"
            >
                <el-table-column
                        prop="name"
                        label="姓名"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="age"
                        label="年龄"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="action"
                        label="操作">
                    <template slot-scope="scope">
                        <el-button
                                @click="removePerson(scope.row._id)"
                                type="text"
                                size="small"
                        >
                            删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'HelloWorld',
    props: {
      msg: String
    },
    mounted() {

    },
    data: function () {
      return {
        rules: {
          username: [
            {required: true, message: '请输入姓名', trigger: 'blur'},
          ],
          age: [
            {required: true, message: '请输入年龄', trigger: 'blur'}
          ],
        },
        userInfo: {
          username: null,
          age: null,
        },
        tableData: [],
      }
    },
    methods: {
      onSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.axios.post('/addPerson', {
              name: this.userInfo.username,
              age: this.userInfo.age,
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      getInfo() {
        this.axios.get('/getPerson').then(res => {
          this.tableData = res.data.results;
        })
      },
      removePerson(_id){
        this.axios.delete('/removePerson',{
          params:{
            _id
          }
        }).then(res=>{
          this.getInfo()
        })
      }
    }
  }
</script>

